.orderProduct {
    display: flex;
    width: 35vw;
    margin: 1vh 2vw;
    border: 0.2vh solid black;
}

.orderProductImg {
    width: 10vw;
    height: 10vw;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.orderProductDescription {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 25vw;
    padding-top: 6vh;
}

.orderProductName {
    margin: 0;
    padding-left: 4vw;
    min-width: 10vw;
    font-family: Comfortaa Light;
}

.orderProductCost {
    margin: 0;
    min-width: 5vw;
    font-family: Comfortaa Light;
}