.basketItem {
    display: flex;
    margin: 2vh;
    border: 1px solid black;
    width: 60vw;
    justify-content: space-between;
}

.basketItem div {
    display: flex;
}

.basketItemImg {
    width: 15vw;
}

.basketItemName {
    margin: 4vh 4vw;
    font-family: Comfortaa Light;
}

.basketItemCost {
    margin: 4vh 2vw;
    font-family: Comfortaa Light;
}

.basketItem span {
    margin: 1vw;
    width: 2vw;
    height: 2vw;
    background-size: cover;
}