.filterWrap {
    border-bottom: 0.3vh solid #000;
    width: 100vw;
}

.filterFlexWrap {
    display: flex;
    justify-content: center;
    padding-top: 2vh;
    flex-wrap: wrap;
}

.filterFlexWrap div {
    display: flex;
    justify-content: center;
    margin: 1vh 2vw;
}

.filterFlexWrap div select {
    padding: 1vh 2vw;
    font-size: 3vh;
    font-family: Comfortaa Light;
}

.filterFlexWrap div select:hover {
    cursor: pointer;
}

.filterFlexWrap div input:hover {
    cursor: pointer;
}

.filterButtonWrap {
    display: flex;
    justify-content: center;
}

.filterButton {
    padding: 1vh 3vw;
    font-size: 3vh;
    background-color: #fff;
    border: 0.1vh solid #000;
    margin: 2vh 4vw;
    font-family: Comfortaa Light;
}

.filterButton:hover {
    cursor: pointer;
}

.filterReset {
    border: none;
    background-color: #fff;
    font-size: 2.5vh;
    text-decoration: underline;
    margin: 2vh 4vw;
    font-family: Comfortaa Light;
}

.filterReset:hover {
    cursor: pointer;
}

option {
    font-size: 2.5vh;
}