.history {
    background-image: url(../../img/history.png);
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    
}

.historyTitle {
    font-family: Comfortaa Light;
    font-size: 5vh;
    color: #fff;
}

@media screen and (max-width: 450px) {
    .history {
        max-height: 30vh;
        min-height: 30vh;
    }
    .historyTitle {
        font-size: 3vh;
    }
}
