.manCategory {
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(../../../img/manCatalog.png);
    background-size: cover;
    background-repeat: no-repeat;
    width: 49.3vw;
    margin-left: 0.1vw;
}

.manCategoryContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    max-height: 100vh;
    min-height: 80vh;
}

.manCategoryName {
    color: #fff;
    font-family: Comfortaa Light;
    font-size: 5vh;
}

@media screen and (min-width:400px) and (max-width:900px) {
    .manCategoryContainer {
        max-height: 90vh;
        min-height: 70vh;
    }
}

@media screen and (max-width: 450px) {
    .manCategory {
        width: 100vw;
        height: 27vh;
        margin: 0 0 0.4vh 0;
        background-position: 0 -20vh;
    }
    .manCategoryContainer {
        width: 100vw;
        min-height: 0;
        height: 27vh;
    }
    .manCategoryName {
        font-size: 3vh;
    }
}