.orderWrap {
    height: 85vh;
    display: flex;
}

.orderSection {
    width: 50vw;
    height: 85vh; 
}

.orderFormWrap {
    border-right: 0.2vh solid black;
}

.orderForm {
    display: flex;
    height: 50vh;
    margin: 15vh 15vw 15vh 5vw;
    flex-direction: column;
    justify-content: space-evenly;
    position: relative;
}

.orderForm h2 {
    margin: 1vh 0;
    font-size: 5vh;
    font-family: Comfortaa Light;
}

.orderForm input, select {
    margin: 1vh 0;
    width: 20vw;
    padding: 1vh 2vw;
    border-radius: 0.2vw;
    border: 0.2vh solid black;
    font-size: 3vh;
}

.orderForm a:hover {
    cursor: auto;
}

.orderButtonWrap {
    width: 19vw;
}

.orderForm button {
    width: 15vw;
    padding: 1vh 2vw;
    font-size: 3vh;
    margin: 5vh 0 0 0;
    background-color: #fff;
    border-radius: 0.2vw;
    border: 0.2vh solid black;
    font-family: Comfortaa Light;

}

.orderForm button:hover {
    cursor: pointer;
}

.orderInfo {
    border-radius: 0.2vw;
}

.orderProducts {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 65vh;
    overflow-y: scroll;
}

.orderInfo .orderInfoTitle, .orderInfoSum {  
    padding: 3vh 2vw;
    margin: 0;
    font-family: Comfortaa Light;
}

.orderInfoTitle {
    border-bottom: 0.2vh solid #000;
}

.orderInfoSum {
    border-top: 0.2vh solid #000;
}