header {
    border-bottom: 0.3vh solid #000;
    position: fixed;
    background-color: #fff;
    height: 14.5vh;
    width: 100vw;
    box-sizing: padding-box;
}

a {
    text-decoration: none;
    color: black;
}

.logoContainer {
    display: flex;
    width: 68vw;
    margin-left: 15vw;  
}
     
.logo {
    font-family: "Lemon/Milk(RUS BY LYAJKA";
    margin: 4vh auto;
    font-size: 5vh;
}

.basketButton {
    height: 10vh;
    position: absolute;
    right: 4vw;
    top: 2vh;
    display: flex;
    align-items: center;
}

.basketButton:hover {
    cursor: pointer;
}

.basketButton p {
    font-size: 3.5vh;
    margin: 0 1vw;
}

.basketLogo {
    height: 10vh;
}

.basketCounter {
    font-family: "Lemon/Milk(RUS BY LYAJKA";
    margin: 4vh 0 0 1vw;
}

@media screen and (min-width: 450px) and (max-width: 800px) {
    .logo {
        font-size: 4vh;
    }
    .basketButton {
        top: 3vh;
        height: 8vh;
    }
    .basketLogo {
        height: 8vh;
    }
}  

@media screen and (max-width: 450px) {
    header {
        height: 10vh;
    }
    .logoContainer {
        height: 10vh;
    }
    .logo {
        font-size: 2.5vh;
    }
    .basketButton {
        top: 3vh;
        height: 5vh;
    }
    .basketLogo {
        height: 5vh;
    }
    .basketButton p {
        font-size: 2.5vh;
        margin: 0.6vw;
    }
}