.womanCategory {
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(../../../img/womanCatalog.png);
    background-size: cover;
    background-repeat: no-repeat;
    width: 49.3vw;
    margin-right: 0.1vw;
}

.womanCategoryContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    max-height: 100vh;
    min-height: 80vh;
}

.womanCategoryName {
    color: #fff;
    font-family: Comfortaa Light;
    font-size: 5vh;
}

@media screen and (min-width:451px) and (max-width:900px) {
    .womanCategoryContainer {
        max-height: 90vh;
        min-height: 70vh;
    }
}

@media screen and (max-width: 450px) {
    .womanCategory {
        width: 100vw;
        margin: 0 0 0.4vh 0;
        height: 26vh;
        background-position: 0 -20vh;
    }
    .womanCategoryContainer {
        width: 100vw;
        height: 26vh;
        border-bottom: 0.4vh solid #fff;
    }
    .womanCategoryName {
        font-size: 3vh;
    }
    
}