.basket {
    display: flex;
    flex-direction: column;
}

.basketExitWrap {
    margin: 4vw;
    text-align: right;
}
.basketExit {
    background-size: cover;
    width: 1vw;
    padding: 1.5vh 1.5vw;
}

.basketContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 10vw 4vw 10vw;
    min-height: 37vh;
    width: 80vw;
}

.basketInfo {
    border-top: 0.4vh solid #000;
    padding: 2vh 10vw;
}

.basketInfo p {
    font-family: Comfortaa Light;
}

.makeOrderButton {
    padding: 1vh 3vw;
    font-size: 3vh;
    background-color: #fff;
    border-radius: 0.2vw;
    border: 0.2vh solid black;
    font-family: Comfortaa Light;
}