.productViewerWrap {
    height: 84.7vh;
    display: flex;
}

.productViewerWrap p {
    font-family: Comfortaa Light;
}

.productViewerImgWrap {
    width: 40vw;
    padding: 4vh 4vw;
    background-size: cover;
    background-position: center;
}

.productViewerImg {
    width: 40vw;
    padding: 4vh 4vw;
}

.productViewerInfo {
    width: 50vw;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.productViewerExit {
    float: right;
    margin-right: 4vw;
    background-size: cover;
    width: 4vw;
    height: 4vw;
}

.productViewerText {
    font-size: 2em;
}

.productViewerButton {
    display: inline-block;
    width: 10vw;
    border-radius: 0.2vw;
    border: 1px solid black;
    padding: 1.5vh 1.5vw;
    font-family: Arial;
    background-color: #fff;
    font-family: Comfortaa Light;
}